import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";

import { Search } from "react-iconly";
import { connect } from "react-redux";
import { isMobile } from "react-device-detect";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";

import {
  getFinanciers,
  financiersEdit,
  financierDelete,
  isModelVisible,
  SetFinanciersParams,
} from "../../../../store/Masters/Financiers/financiersSlice";
import AddFinanciers from "./addFinanciers";

function Financiers(props) {
  useEffect(() => {
    props.getFinanciers({ page: 1, search: "", page_size: 10 });
    // props.getMiniFirm();
  }, []);
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Code",
      dataIndex: "code",
      fixed: isMobile ? null : "left",
      filterSearch: true,
      width: 100,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Financiers Name",
      dataIndex: "name",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterMode: "tree",
      filterSearch: true,
      // onFilter: (value, record) => record.name.includes(value),
      width: 150,
    },
    {
      title: "Bank",
      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: 150,
      render: (row) => <span className={columnClass}>{row.bank?.name}</span>,
    },
    {
      title: "Mobile",
      filterMode: "tree",
      dataIndex: "mobile",
      // onFilter: (value, record) => record.name.includes(value),
      width: 120,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Company Name",
      filterMode: "tree",
      dataIndex: "companyname",
      // onFilter: (value, record) => record.name.includes(value),
      width: 150,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Aadhar Number",
      filterMode: "tree",
      dataIndex: "aadharno",
      // onFilter: (value, record) => record.name.includes(value),
      width: 150,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Account Number",
      filterMode: "tree",
      dataIndex: "accono",
      // onFilter: (value, record) => record.name.includes(value),
      width: 150,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "GST Number",
      filterMode: "tree",
      dataIndex: "gstno",
      // onFilter: (value, record) => record.name.includes(value),
      width: 150,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "IFSC CODE",
      filterMode: "tree",
      dataIndex: "ifsccode",
      // onFilter: (value, record) => record.name.includes(value),
      width: 150,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Address",
      filterMode: "tree",
      dataIndex: "companyaddress",
      // onFilter: (value, record) => record.name.includes(value),
      width: 150,
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Actions",
      key: "action",
      width: 150,
      fixed: isMobile ? null : "right",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_financier") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.financiersEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_financier") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.financierDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
          )}
        </Space>
      ),
    },
  ];

  const data = props.financierslist.map((row, key) => ({
    ...row,
    index:
      (props.financiersParams.page - 1) * props.financiersParams.page_size +
      key +
      1,
    // mobilename: row.mobile.name,
    // aadharnoname: row.aadharno.name,
    // ifsccodename: row.ifsccode.name,
    // accononame: row.accono.name,
    // addressname: row.address.name,
  }));

  // function onShowSizeChange(current, pageSize) {
  //   props.getCountrys({ page_size: pageSize, page: 1 });
  // }
  function onPageChange(page, pageSize) {
    props.SetFinanciersParams({
      ...props.financiersParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getFinanciers({
      ...props.financiersParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetFinanciersParams({ ...props.financiersParams, search: val });
    props.getFinanciers({ ...props.financiersParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Financiers Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_financier") !== -1 &&
          "Add Financier"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-10">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getFinanciers({
                      ...props.financiersParams,
                      page: 1,
                      search: "",
                    });
                    props.SetFinanciersParams({
                      ...props.financiersParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.financiersParams?.page}
                pageSize={props.financiersParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                // onShowSizeChange={onShowSizeChange}
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddFinanciers />
    </Row>
  );
}

function mapStateToProps({ financiers, system }) {
  return {
    financierslist: financiers.financierslist,
    noofpages: financiers.noofpages,
    listCount: financiers.listCount,
    financiersParams: financiers.financiersParams,
    loading: financiers.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getFinanciers,
  financiersEdit,
  financierDelete,
  isModelVisible,
  // getMiniFirm,
  SetFinanciersParams,
})(Financiers);
