import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  financierAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Financiers/financiersSlice";
import { getMiniBanks } from "../../../../store/mini/miniSlice";
import NumericInput from "../../../components/data-entry/inputs/numberInput";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddFinanciers = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniBanks();
  }, []);
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // const financiersTypeList = [
  //   { id: 1, name: "Internal" },
  //   { id: 2, name: "External" },
  // ];

  console.log("props.rowdata", props.rowdata);

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.rowdata?.name,
      mobile: props.rowdata.mobile,
      aadharno: props.rowdata.aadharno,
      companyname: props.rowdata.companyname,
      gstno: props.rowdata.gstno,
      bank_id: props.rowdata.bank_id,
      accono: props.rowdata.accono,
      ifsccode: props.rowdata.ifsccode,
      companyaddress: props.rowdata.companyaddress,
    };
    if (obj.name != null || obj.name == "") {
      props.financierAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const obj = {
      id: props.rowdata?.id,
      name: props.rowdata?.name,
      mobile: props.rowdata.mobile,
      aadharno: props.rowdata.aadharno,
      companyname: props.rowdata.companyname,
      gstno: props.rowdata.gstno,
      bank_id: props.rowdata.bank_id,
      accono: props.rowdata.accono,
      ifsccode: props.rowdata.ifsccode,
      companyaddress: props.rowdata.companyaddress,
    };
    props.financierAdd(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.rowdata.id == null || props.rowdata.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.rowdata.id == null || props.rowdata.id == 0 ? "Add" : "Update"}{" "}
      Financier
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.rowdata.name,
      mobile: props.rowdata.mobile,
      aadharno: props.rowdata.aadharno,
      companyname: props.rowdata.companyname,
      gstno: props.rowdata.gstno,
      bank_id: props.rowdata.bank_id,
      accono: props.rowdata.accono,
      ifsccode: props.rowdata.ifsccode,
      companyaddress: props.rowdata.companyaddress,
    });
  }, [props.rowdata]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={716}
    >
      <Form
        layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.rowdata.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row>
          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Financier  :"
              name="name"
              rules={[
                { required: true, message: "Please input your Financier!" },
                {
                  pattern: new RegExp(
                    /^[a-zA-Z][a-zA-Z\s]+$/i
                  ),
                  message: "field does not accept numbers",
                }
              ]}
              hasFeedback>
              <Input
                id="Financiers"
                name="name"
                style={{ width: "100%" }}
                placeholder="Financier "
                value={props.rowdata?.name}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Mobile :"
              name="mobile"
              rules={[
                { required: true, message: "Please input your Mobile!" },
                {
                  pattern: new RegExp(/^\d{10}$/g),
                  message: "field does not accept mobile number",
                },
              ]}
              getValueProps={() => { }}
              hasFeedback
            >
              <NumericInput
                id="Mobile"
                name="mobile"
                style={{ width: "100%" }}
                placeholder="Mobile Number"
                maxLength={10}
                value={
                  props.rowdata.mobile == null ? "" : props.rowdata?.mobile
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Aadhar Number :"
              name="aadharno"
              rules={[
                { required: true, message: "Please input your Aadhar Number!" },
                {
                  pattern: new RegExp(/^\d{12}$/g),
                  message: "field does not accept aadhar number",
                },
              ]}
              getValueProps={() => { }}
              hasFeedback
            >
              <NumericInput
                id="Aadharno"
                name="aadharno"
                style={{ width: "100%" }}
                placeholder="Aadharno Number"
                maxLength={12}
                value={
                  props.rowdata.aadharno == null ? "" : props.rowdata?.aadharno
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Company Name :"
              name="companyname"
              rules={[
                { required: true, message: "Please input your Company Name!" },
                {
                  pattern: new RegExp(
                    /^[a-zA-Z][a-zA-Z\s]+$/i
                  ),
                  message: "field does not accept numbers",
                }
              ]}
              hasFeedback
            >
              <Input
                id="Company Name"
                name="companyname"
                style={{ width: "100%" }}
                placeholder="Company Name"
                value={props.rowdata?.companyname}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="GST Number :"
              name="gstno"
              rules={[
                { required: true, message: "Please input your GST Number!" },
                {
                  min: 15,
                  message: "GST Number must be minimum 15 characters.",
                },
              ]}
              getValueProps={() => { }}
              hasFeedback>
              <Input
                id="GST"
                name="gstno"
                style={{ width: "100%" }}
                placeholder="GST Number"
                maxLength={16}
                value={props.rowdata?.gstno}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Bank :"
              name="bank_id"
              rules={[{ required: true, message: "Please select your Bank!" }]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select  Bank"
                optionFilterProp="bank"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "bank_id",
                    value: value,
                  });
                }}
                value={
                  props.rowdata.bank_id == null ? "" : props.rowdata?.bank_id
                }
              >
                {props.miniBanksList.map((bank, index) => {
                  return (
                    <Option key={index} value={bank.id}>
                      {bank.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Account Number :"
              name="accono"
              rules={[
                {
                  required: true,
                  message: "Please input your Account Number!",
                },
                {
                  min: 9,
                  message: "Account Number Must be minimum 9 characters"
                }
              ]}
              getValueProps={() => { }}
              hasFeedback>
              <NumericInput
                id="accono"
                name="accono"
                style={{ width: "100%" }}
                placeholder="Account Number"
                maxLength={18}
                value={
                  props.rowdata.accono == null ? "" : props.rowdata?.accono
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="IFSC Code :"
              name="ifsccode"
              rules={[
                { required: true, message: "Please input your IFSC Code!" },
                {
                  pattern: new RegExp(/[A-Z]{4}[0][a-zA-Z0-9]{6}$/i),
                  message: "field does not accept IFSC code",
                },
              ]}
              hasFeedback
            >
              <Input
                id="IFSC-Code"
                name="ifsccode"
                style={{ width: "100%" }}
                placeholder="IFSC Code"
                value={props.rowdata?.ifsccode}
                onChange={(e) => {
                  props.InputChangeValue({
                    key: e.target.name,
                    value: e.target.value.toUpperCase(),
                  });
                }}
              />
            </Form.Item>
          </Col>

          <Col md={24} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Company Address :"
              name="companyaddress"
              rules={[
                {
                  required: true,
                  message: "Please input your Company Address !",
                },
              ]}
              hasFeedback>
              <Input.TextArea
                id="Address"
                name="companyaddress"
                style={{ width: "100%" }}
                maxLength={150}
                placeholder="Company Address"
                value={props.rowdata?.companyaddress}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ financiers, mini }) {
  return {
    rowdata: financiers.rowdata,
    model: financiers.model,
    miniBanksList: mini.miniBanksList,
    // miniFirmList: mini.miniFirmList,
  };
}

export default connect(mapStateToProps, {
  financierAdd,
  isModelVisible,
  InputChangeValue,
  getMiniBanks,
})(AddFinanciers);
